<script>

export default {
  name: 'App',
  components: {
  },
  directives: {
    unmute: {
      inserted(el) {
        // el.removeAttribute("muted");
        el.play()
      }
    }
  },
  data() {
    return {
      position: ["red"]
    }
  }
}
</script>

<style>
  #app {
    background-color: black;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .window {
    width: 800px;
    height: 600px;
    border: 1px solid rgba(255, 255, 255, 0.171);
    position: relative;
    overflow: hidden;
  }
  .bg {
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    position: absolute;
  }
  .video {
    position: absolute;
    left: 410px;
    top: 140px;
    width: 300px;
    height: 200px;
    outline: none !important;
  }
  .video:focus {
    outline: none !important;
  }
  .touch_field {
    position: absolute;
    cursor: pointer;
  }
  .pick_book {
    width: 120px;
    height: 180px;
    left: 357px;
    top: 237px;
  }
</style>

<template>
  <div id="app">
    <div class="window">
      <div class="scene" v-if="position.includes('red')">
        <img src="./assets/red_base.jpg" class="bg" />
        <div class="touch_field pick_book" v-on:click="() => {position = ['red', 'book', 'open']}" />
        <div class="scene" v-if="position.includes('book')">
          <div class="scene" v-if="position.includes('open')">
            <video class="video" v-unmute>
              <source src="./assets/shia.mp4" type="video/mp4" />
            </video>
            <img src="./assets/red_open.png" class="bg" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>



